.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-logo {
  height: 160px;
  pointer-events: none;
}

.numeric {
  font-family: 'Source Code Pro', monospace;
}

.rainbow-text {
  background-image: linear-gradient(to left, violet, indigo, blue, green, yellow, orange, red);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

.animated-rainbow-bg {
  background: linear-gradient(124deg, #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8, #dd00f3, #dd00f3);
  background-size: 1800% 1800%;
  -webkit-animation: animated-rainbow-bg 18s ease infinite;
  -z-animation: animated-rainbow-bg 18s ease infinite;
  -o-animation: animated-rainbow-bg 18s ease infinite;
  animation: animated-rainbow-bg 18s ease infinite;
}

.animated-rainbow-bg:hover {
  opacity: 0.92;
}

.animated-rainbow-bg:active {
  opacity: 0.84;
}

@-webkit-keyframes animated-rainbow-bg {
  0%{background-position:0% 82%}
  50%{background-position:100% 19%}
  100%{background-position:0% 82%}
}
@-moz-keyframes animated-rainbow-bg {
  0%{background-position:0% 82%}
  50%{background-position:100% 19%}
  100%{background-position:0% 82%}
}
@-o-keyframes animated-rainbow-bg {
  0%{background-position:0% 82%}
  50%{background-position:100% 19%}
  100%{background-position:0% 82%}
}
@keyframes animated-rainbow-bg {
  0%{background-position:0% 82%}
  50%{background-position:100% 19%}
  100%{background-position:0% 82%}
}

.content {
  width: 100%;
  max-width: 1280px;
  padding-left: 16px;
  padding-right: 16px;
  box-sizing: border-box;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    /*animation: App-logo-spin infinite 20s linear;*/
  }
}

.App-header {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #000;
}

.App-link {
  color: #61dafb;
}

h1 {
  font-size: 52px;
}

.logo {
  font-family: 'Avenir Next', 'Avenir', monospace;
}

h3 {
  font-size: 16px;
  font-weight: normal;
  line-height: 1.5;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.backButton {
    color: blue;
    text-decoration: none;
}
